.controls-slider__btn:focus {
  outline: none;
}

.controls-slider__btn:hover:not(:disabled), .controls-slider__btn:focus:not(:disabled) {
  fill: black;
  --fill: #fff;
  opacity: 1;
}

.controls-slider__btn:disabled {
  cursor: not-allowed;
}
